

import useVuelidate from '@vuelidate/core'
import { required, email ,helpers} from '@vuelidate/validators'


export default {
  name: "Form",

  setup() {

    return {
      v$: useVuelidate(),

    }

  },

  data() {
    return {
      email: '',
      company: '',
      name: ''

    };
  },


  validations() {
    return {
      name: {required: helpers.withMessage('Bitte das Feld ausfüllen!', required)},
      email: {
        required: helpers.withMessage('Bitte das Feld ausfüllen!', required),
        email: helpers.withMessage('E-Mail ist nicht gültig!', email)
      },
      company: {required: helpers.withMessage('Bitte das Feld ausfüllen!', required)},
    }

  },
  methods: {
    onSubmit() {
      console.log('submit')
    }
  }

}
